/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { Card, CardContent, Grid, Chip, makeStyles, Box } from '@material-ui/core';
import PostHeader from 'components/Post/PostHeader';
import Markdown from 'components/Utils/Markdown';
import RelatedPosts from 'components/Cards/RelatedPosts';
import * as Icon from 'react-feather';
import OtherLangPosts from 'components/Lists/OtherLangPosts';
import { Post } from 'models/post';

interface Props {
  post: Post;
  otherLangPosts: Array<Pick<Post, 'fields'>>;
}
const PostCard = ({ post, otherLangPosts }: Props) => {
  const {
    frontmatter: { title, date, categories, tags, thumbnail },
    html,
  } = post;
  const marginTopStyle = makeStyles({
    root: {
      marginTop: '2rem',
    },
  })();

  return (
    <Card>
      {thumbnail && (
        <Thumbnail>
          <img src={thumbnail?.childImageSharp?.fluid?.src} alt={title} title={title} />
        </Thumbnail>
      )}
      <CardContent component="article">
        <PostHeader title={title ?? ''} date={date} categories={categories ?? []} />
        {otherLangPosts.length > 0 && (
          <Box
            css={css`
              margin: 2rem 0;
            `}
          >
            <OtherLangPosts otherLangPosts={otherLangPosts} />
          </Box>
        )}
        <Markdown html={html ?? ''} />
        <RelatedPosts className={marginTopStyle.root} tags={tags ?? []} currentPost={post} />
        {tags && (
          <Grid container className={marginTopStyle.root} component="ul" spacing={1}>
            <Grid
              item
              component="li"
              css={css`
                list-style: none;
                margin: 0;
                padding: 0;
              `}
            >
              <Icon.Tag />
            </Grid>
            {tags.map((tag, index) => (
              <Grid
                item
                component="li"
                key={index}
                css={css`
                  list-style: none;
                  margin: 0;
                  padding: 0;
                `}
              >
                <Chip label={tag} size="small" />
              </Grid>
            ))}
          </Grid>
        )}
      </CardContent>
    </Card>
  );
};

export default PostCard;

const Thumbnail = styled.div`
  padding-top: 42.8%;
  display: block;
  position: relative;
  img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin: 0;
  }
`;
