/** @jsx jsx */
import { jsx } from '@emotion/core';
import { SupportedLanguages } from 'src/models/site';
import { useMemo, ReactNode, Fragment } from 'react';

interface Props {
  lang: SupportedLanguages;
}
const LanguageFlag = ({ lang }: Props) => {
  const flag: { [key in SupportedLanguages]: ReactNode } = useMemo(
    () => ({
      ko: (
        <span role="img" aria-label="ko">
          
        </span>
      ),
      en: (
        <span role="img" aria-label="en">
          
        </span>
      ),
    }),
    []
  );

  return <Fragment>{flag[lang]}</Fragment>;
};

export default LanguageFlag;
